.faq {
    margin-bottom: 50px;
}

.faq-section {
    padding-top: 10px;
    font-size: 28px;
    z-index: 100;
    background: transparent;
}

.faq-cover {
    height: 750px;
    width: 100vw;
    margin: 0;
    padding: 0;
    background-size: cover;
    background: #9893e4 no-repeat fixed center;
  }

.faq-title {
    font-size: 48px;
    -webkit-transform: translateY(350px);
            transform: translateY(350px);
    color: #ffffff;   
}

.qanda {
    border-width: 0 0 3px 0;
    border-radius: 0;
    border-color: #9893e4;
    border-style: solid;
    width: 60%;
}

.question {
    margin-top: 15px;
    margin-bottom: 15px;
    font-family: 'Muli', sans-serif;
    background-color: transparent;
    float: left;
}

.question:hover {
    cursor: pointer;
}

.question-clicked {
    border-width: 0;
    transition-delay: 0s;
}

.answer {
    font-size: 20px;
    font-weight: 300;
    font-family: 'Muli', sans-serif;
    opacity: 0;
    position: relative;
    max-height: 0;
    transition: 0.6s;
    margin-bottom: 15px;
}

.answer-clicked {
    top: 0px;
    transition: 1s;  
    max-height: 100px;
    opacity: 1;
}

.nopadding {
    padding: 0;
}

@media (max-width: 1200px) {
    .faq {
        font-size: 24px;
    }
}

@media (max-width: 767px) {
    .faq-cover {
        height: 300px;
    }

    .faq-title {
        font-size: 40px;
        -webkit-transform: translateY(100px);
                transform: translateY(100px);
    }

    .question {
        width: 100%;
    }
}
