.slide-container {
  width: 70%;
  margin: auto;
}

.each-slide > div {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-size: cover;
  height: 75vh;
}

