.previous-arrow {
  position: absolute;
  top: 50%;
  left: 17px;
  padding: 5px;
  border-radius: 100%;
  font-size: 20px;
  background-color: #f3f3f3;
  height: 50px;
  width: 50px;
  -webkit-justify-content: center;
          justify-content: center;
  display: -webkit-flex;
  display: flex;
}

.next-arrow {
  position: absolute;
  top: 50%;
  right: 17px;
  padding: 5px;
  border-radius: 100%;
  font-size: 20px;
  background-color: #f3f3f3;
  height: 50px;
  width: 50px;
  -webkit-justify-content: center;
          justify-content: center;
  display: -webkit-flex;
  display: flex;
}

.previous-arrow img {
  -webkit-transform: translate(-2px);
          transform: translate(-2px);
}

.next-arrow img {
  -webkit-transform: translate(2px);
          transform: translate(2px);
}

